.advantages {
  background: url("../../img/home-background-decore-1.png") no-repeat center/373px 361px, url("../../img/home-background-decore-2.png") no-repeat 12% 88%/336px 325px, var(--primary-accent-color);
  margin: 0;
}
.advantages .container {
  padding: 120px 15px;
  position: relative;
}
.advantages .container .image {
  position: absolute;
  top: -5%;
  right: 0;
}
@media screen and (max-width: 980px) {
  .advantages .container .image {
    display: none;
  }
}
.advantages-title {
  margin: 0;
  font-family: "RubikMonoOne";
  font-style: normal;
  font-weight: 400;
  font-size: 38px;
  line-height: 47px;
  color: var(--dark-color);
}
.advantages__list {
  margin-top: 80px;
  max-width: 988px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  color: var(--dark-color);
}
.advantages__list span {
  font-weight: 500;
}
.advantages__list-item:not(:first-child) {
  margin-top: 24px;
}
.advantages-cta {
  margin-top: 40px;
}
@media screen and (max-width: 980px) {
  .advantages {
    background: url("../../img/home-decore-mobile-1.png") no-repeat top right/373px 361px, url("../../img/home-decore-mobile-2.png") no-repeat center/373px 361px, url("../../img/home-decore-mobile-3.png") no-repeat bottom left/336px 325px, var(--primary-accent-color);
  }
  .advantages-title {
    font-size: 26px;
    text-align: center;
  }
  .advantages__list {
    margin: 0 auto;
    margin-top: 20px;
    font-size: 20px;
  }
  .advantages__list-item:not(:first-child) {
    margin-top: 18px;
  }
  .advantages .container {
    padding: 90px 15px;
  }
  .advantages-cta {
    margin: 0 auto;
    display: block;
    margin-top: 30px;
  }
}
@media screen and (max-width: 480px) {
  .advantages {
    background: url("../../img/home-decore-mobile-1.png") no-repeat top right/373px 361px, url("../../img/home-decore-mobile-2.png") no-repeat left center/373px 361px, url("../../img/home-decore-mobile-3.png") no-repeat 10% 97%/336px 325px, var(--primary-accent-color);
  }
  .advantages__list {
    font-weight: 300;
    max-width: 400px;
    line-height: normal;
  }
  .advantages__list span {
    font-weight: 400;
  }
}